import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Optional,
  Output,
  Self,
  SimpleChanges,
} from '@angular/core';
import { NgControl } from '@angular/forms';
import { BcHelperService, BcGeneralInputComponent } from '@bancolombia/design-system-web/bc-helpers';
import { BcPlannerBehavior, IBcPlannerItem, IBcPlannerChanges} from '@bancolombia/design-system-behaviors';

/**
 * Importa el componente de calendario con los diseños de Bancolombia S.A.
 *
 * @export
 * @class BcPlannerComponent
 * @implements {OnInit}
 * @implements {AfterViewInit}
 */
@Component({
  selector: 'bc-planner',
  templateUrl: './bc-planner.component.html',
})
export class BcPlannerComponent extends BcGeneralInputComponent implements OnInit, AfterViewInit, OnChanges {
  element: BcPlannerBehavior;

  /**
   * Id del elemento planner.
   *
   * @type {string}
   * @memberOf BcPlannerComponent
   */
  @Input() componentId = this.bcHelperService.getId(this);

  /**
   * Dato de entrada que recibe los items a mostrar.
   *
   * @type {string}
   * @memberOf BcPlannerComponent
   */
  @Input() items: IBcPlannerItem[] = [];

  /**
   *.
   *
   * @type {string}
   * @memberOf BcPlannerComponent
   */

  @Input() itemsTop: IBcPlannerItem[] = [];

  /**
   * Dato de salida que muestra cuando cambia el valor.
   *
   * @type {string}
   * @memberOf BcPlannerComponent
   */

  @Output() changeValue: EventEmitter<any> = new EventEmitter<any>();
  changesBehavior: IBcPlannerChanges;
  today: Date = new Date();
  todayMonth: number = this.today.getMonth();
  todayYear: number = this.today.getFullYear();

  constructor(@Self() @Optional() public controlDirective: NgControl, private bcHelperService: BcHelperService, private changeDetectorRef: ChangeDetectorRef) {
    super();
    this.element = new BcPlannerBehavior();
    this.element.outputChanges = (value: IBcPlannerChanges) => {
      this.changesBehavior = value;
      this.setMonth_Year();
    };
    this.element.outputValues = (value: any) => {
      this.onInput(value);
    };
    if (controlDirective) {
      controlDirective.valueAccessor = this as BcGeneralInputComponent;
    } 
  }

  ngOnInit() {}
  ngAfterViewInit() {
    this.element.setId(this.componentId);
    this.element.setUp();
    if (this.items) {
      this.element.setItems(this.postItems(this.items));
    }
    if (this.itemsTop) {
      this.element.setItemsTop(this.itemsTop);
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes) {
      this.changeDetectorRef.detectChanges();
      if (changes.items) {
        this.element.setItems(this.postItems(changes.items.currentValue));
      }
    }
  }

  postItems(Items: IBcPlannerItem[]) {
    return Items.filter((a) => {
      if (a.date.getMonth() == this.todayMonth && a.date.getFullYear() == this.todayYear) {
        return true;
      }
      return false;
    });
  }

  setMonth_Year() {
    this.todayMonth = this.changesBehavior.monthValue;
    this.todayYear = this.changesBehavior.yearValue;
    if (this.items) {
      this.element.setItems(this.postItems(this.items));
    }
  }

  onInput(value: any): void {
    this.changeValue.emit(value);
  }
}
